import React, { useState } from 'react';
import { useRouter } from '@unexp/router'
import { Button, FormItem, Input, Panel, PanelHeaderBack, PanelHeader, View, Group, FormStatus, Link } from '@vkontakte/vkui';
import Status from './Status';

const Replenishmen = ({ id, properties }) => {
    let { back } = useRouter();
    const [value, setValue] = useState(20);

    /* Функция пополнения */
    async function replenishmen(quantity) {

        // replenishment = пополнение 
        // quantity = количество
        const md5 = require('md5');
        let $shop_id = '4629';
        let $secret_key = 'Y5e5uGB9JyGHhvuwiymBVA2d6D';
        let $amount = Number(quantity);
        let $pay_id = 10000000 + Math.random() * (19999999 + 1 - 10000000);
        $pay_id = Math.floor($pay_id);
        let $currency = 'RUB';
        let $sign = md5(`${$currency}:${$amount}:${$secret_key}:${$shop_id}:${$pay_id}`);
        let answer = `https://any-pay.org/merchant?merchant_id=${$shop_id}&amount=${$amount}&pay_id=${$pay_id}&currency=RUB&desc=LikeBot пополнение личного счета&vkid=${properties.vk}&sign=${$sign}`;

        console.log(answer)
        return answer;
    }


    return (
        <View activePanel={id}>

            <Panel id="replenishmen">

                <PanelHeader left={<PanelHeaderBack onClick={back} />}>
                    Пополнение
        </PanelHeader>

                <Status properties={properties}></Status>
                <Group>
                    <FormStatus header="Введите сколько хотите пополнить" mode="default">
                        <FormItem status="valid" top="Число">
                            <Input value={value} onChange={e => setValue(e.target.value)} type="number" min="1" max="1000" placeholder="Например: 10" required />
                        </FormItem>
                        <Link href={replenishmen(value)} target="_blank" >
                            <Button size="l" stretched>Пополнить</Button>
                        </Link>
                    </FormStatus>
                </Group>
            </Panel>

        </View>

    )
};


export default Replenishmen;
