import React from 'react';
import { useRouter, usePopout } from '@unexp/router'
import PropTypes from 'prop-types';
import axios from 'axios';
import { Counter, List, View, HorizontalCell, HorizontalScroll, Alert, Link } from '@vkontakte/vkui'
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import { Icon28MarketLikeOutline, Icon28ListLikeOutline, Icon20DonateCircleFillYellow, Icon20MoneyCircleOutline, Icon20FavoriteCircleFillYellow, Icon20NarrativeOutline } from '@vkontakte/icons'
import img_vk from '../img/img_vk.jpg'; // Картинка для группы

const largeImageStyles = { // Стиль
	width: 220,
	height: 124,
	borderRadius: 8,
	border: '1px solid var(--placeholder_icon_background)',
	objectFit: 'cover'
};

const Home = ({ fetchedUser, properties, id, API_KEY }) => {
	let { push } = useRouter();
	let { setPopout, closePopout } = usePopout();

	/* Акционная функция */
	async function stock(api, id) {
		await axios.get(`https://likebot.ru/stock?token=${api}&id=${id}`)
			.then(async function (response) {
				if (response.status === 204) { // Если ответ отрицательный
					return setPopout(
						<Alert

							actions={[
								{ title: 'Хорошо, сейчас!', autoclose: true, mode: 'cancel' },
							]}
							actionsLayout='vertical'
							onClose={closePopout}
							header='Вам необходимо поставить сердечко под последним постом '
							text='Потом повторите попытку и мы СРАЗУ ЖЕ Вас возьмём в ЛТ'
						/>
					)
				}
				console.log(response.status); // Status ответа
				console.log(response.data); // Что отвечает сервер
				setPopout(
					<Alert
						actions={[
							{ title: 'Супер!', mode: 'cancel', autoclose: true },
						]}
						actionsLayout='vertical'
						onClose={closePopout}
						header='Успех'
						text='Вы успешно выполнили задание, Вы уже на стеночке! А ещё мы выдали Вам VIP статус на 2 дня, ознакомьтесь с приложением :)'
					/>
				)
			})
			.catch(function (err) {
				console.log("ERROR SYKa BLYAT: ", err)
			})
	}

	return (
		<View activePanel={id}>
			<Panel id="home">
				<PanelHeader>Ваша информация</PanelHeader>
				{fetchedUser &&
					<Group title="User Data Fetched with VK Bridge and API DataBase">
						<Cell
							before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200} /> : null}
							description={properties.vk && `🆔 ${properties.vk}`}
							indicator={properties.vip?.access ? `👑 VIP 👑` : null}
						>

							{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
						</Cell>
					</Group>}

				<Group title="Navigation Block">
					<Div>
						<Cell size="xl" onClick={() => push({ view: 'info', panel: 'info' })} expandable before={<Icon28ListLikeOutline />}>За что дают баллы?</Cell>
						<Cell size="xl" onClick={() => push({ view: 'shop', panel: 'shop' })} expandable before={<Icon28MarketLikeOutline />}>Магазин</Cell>
					</Div>
				</Group>
				<Group info="Information for user">
					<List>
						<Cell size="xl" onClick={() => push({ view: 'info', panel: 'info' })} before={<Icon20DonateCircleFillYellow />} indicator={<Counter mode="primary">{properties.balance}</Counter>}>Баллы</Cell>
						<Cell onClick={() => push({ view: 'replenishmen', panel: 'replenishmen' })}
							before={<Icon20MoneyCircleOutline />} indicator={<Counter mode="primary">{properties.rub}</Counter>}>Рубли</Cell>
						<Cell size="xl" onClick={async () =>
							setPopout(
								<Alert
									actions={[
										{ title: 'Понятно', autoclose: true, mode: 'cancel' },
									]}
									actionsLayout='vertical'
									onClose={closePopout}
									header='В разработке'
									text='В дальнейшем при нажатие на эту кнопку, Вам будет показана история когда и как Вам дали баллы'
								/>
							)} before={<Icon20FavoriteCircleFillYellow />} indicator={<Counter mode="primary">{properties.points}</Counter>}>Заработано баллов сегодня</Cell>
						<Cell onClick={async () =>
							setPopout(
								<Alert
									actions={[
										{ title: 'Понятно', autoclose: true, mode: 'cancel' },
									]}
									actionsLayout='vertical'
									onClose={closePopout}
									header='Уведомления приходят в Личные Сообщения с сообществом'
									text='[ВАЖНО] Вы должны хоть раз написать в ЛС группе'
								/>
							)}
							before={<Icon20NarrativeOutline />} indicator={<Counter mode="primary">{properties.alert ? 'Вкл' : 'Выкл'}</Counter>}>Уведомления о поступлении баллов</Cell>
					</List>
				</Group>
				<Group info="Акция!">
					<HorizontalScroll>
						<div style={{ display: 'flex' }}>
							{!properties.stock ?
								<HorizontalCell onClick={async () => { (stock(API_KEY, properties.vk)) }} size='l' header='Хочу в ЛТ' subtitle="Жми чтобы попасть!">
									<img alt="stock" style={largeImageStyles}
										src="https://i.pinimg.com/originals/01/05/cd/0105cd8de9af870f76b525412fc1acdf.png" />
								</HorizontalCell> : null}

							<Link href="https://vk.com/bots_likes" target="_blank" >
								<HorizontalCell size='l' header='Группа' subtitle="Приложения">
									<img alt="stock" style={largeImageStyles}
										src={img_vk} />
								</HorizontalCell>
							</Link>

							<HorizontalCell onClick={() => push({ view: 'roulette', panel: 'roulette' })} size='l' header='Рулетка' subtitle="Крутить рулетку">
								<img alt="stock" style={largeImageStyles}
									src={img_vk} />
							</HorizontalCell>

						</div>
					</HorizontalScroll>
				</Group>

			</Panel>
		</View>
	)
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
	properties: PropTypes.shape({
		balance: PropTypes.number,
		rub: PropTypes.number,
		points: PropTypes.number,
		alert: PropTypes.bool,
		vk: PropTypes.number.isRequired,

	})
};

export default Home;
